.DialogHRMOverlay {
	height: 100vh;
	width: 100%;
	opacity: 0;
	position: fixed;
	z-index: 210;
	left: 0;
	bottom: 0;
	background-color: transparent;
	overflow-x: hidden;
	transition: opacity ease-in-out 0.5s;
	transform-origin: 50% 210% 0;
}

.active.DialogHRMOverlay {
	opacity: 1;
	bottom: 0;
	transition: opacity ease-in-out 0.358s;
}
